@font-face {
  font-family: Sectra;
  src: url("../fonts/sectra.ttf") format("truetype"); }

/*To fixies the weird white bar on the right thingie and also so it does wanna scroll sideways*/
.container-fluid {
  padding-right: 0;
  /*15px in bootstrap.css*/
  padding-left: 0;
  /*idem*/
  margin-right: auto;
  margin-left: auto; }

.row {
  margin-right: -15px;
  margin-left: -15px; }

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Noto Sans', sans-serif;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  letter-spacing: 0.5px; }

p {
  font-size: 9pt; }

/*Splash image, somehow magically keeps the splash image in place so its not moving!*/
.splash:before {
  content: " ";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  font-size: 0; }

.splash {
  z-index: 1;
  background: no-repeat;
  max-height: 100%;
  background-position: center center;
  background-size: cover; }

.grad-fade {
  height: 100%;
  width: 100%;
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 81%, white 100%);
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 81%, white 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=0 ); }

/*Type stuffs*/
@media screen and (max-width: 1000px) {
  .centering {
    text-align: left;
    padding-left: 20%; } }

.heading {
  padding: 0 0 0 0;
  height: 734px; }

.whitetext {
  color: #fff !important; }

#disqus_thread {
  overflow: hidden; }
  #disqus_thread iframe {
    margin-top: -80px;
    margin-bottom: -54px; }

sup {
  font-family: 'Noto Sans', sans-serif;
  height: 0;
  line-height: 1;
  vertical-align: baseline;
  vertical-align: top;
  position: relative;
  bottom: 1ex;
  font-size: 25%; }

h1 {
  margin-right: 40%;
  font-family: Sectra, sans-serif;
  font-weight: normal;
  font-size: 3.5em;
  color: #000; }

@media screen and (max-width: 1000px) {
  h1 {
    margin-right: 20%;
    font-family: Sectra, sans-serif;
    font-weight: normal;
    font-size: 1.5em;
    color: #000; } }

h2 {
  padding: 5px 0 5px 5px;
  font-family: 'Noto Sans', sans-serif;
  font-weight: normal;
  font-size: 8pt;
  color: #231f20; }

.sub-header {
  padding-bottom: 10px; }

/*
* Sidebar
*/
/*scrollbar sidebar hiding code*/
::-webkit-scrollbar {
  display: none; }

/* Hide for mobile, show later */
.sidebar {
  display: inherit; }

@media (min-width: 768px) {
  .sidebar {
    font-size: 8pt;
    position: relative;
    top: 40px;
    z-index: 1000;
    display: block;
    padding: 0 0 0 50px;
    overflow-x: hidden;
    overflow-y: scroll;
    /* Scrollable contents if viewport is shorter than content. */
    line-height: 80%; } }

@media (max-width: 768px) {
  .sidebar {
    font-size: 8pt;
    top: -450px;
    bottom: 0;
    left: 8%;
    z-index: 1000;
    padding: 0 0 0 50px;
    overflow-x: hidden;
    overflow-y: auto;
    /* Scrollable contents if viewport is shorter than content. */ } }

/* Sidebar navigation */
@media (max-width: 768px) {
  .nav-sidebar {
    margin-right: -21px;
    /* 20px padding + 1px border */
    margin-bottom: 20px;
    margin-left: -20px;
    letter-spacing: 0.5px;
    height: 300px;
    z-index: 100; } }

.nav-sidebar {
  margin-right: -21px;
  /* 20px padding + 1px border */
  margin-bottom: 20px;
  margin-left: -20px;
  letter-spacing: 0.5px; }

.nav-sidebar > li > a,
.nav-sidebar > li > a:hover,
.nav-sidebar > li > a:focus {
  color: #231f20;
  text-decoration: none;
  background-color: transparent;
  padding-top: 4px;
  padding-right: 0;
  padding-left: 20px; }

.nav-sidebar > .active > a,
.nav-sidebar > .active > a:hover,
.nav-sidebar > .active > a:focus {
  color: #fff;
  background-color: transparent; }

.user-active {
  background-color: #0f0fff;
  color: #fff; }

/*
* Main content
*/
.main {
  padding: 20px; }

@media (min-width: 768px) {
  .main {
    padding-right: 40px;
    padding-left: 40px; } }

.main .page-header {
  margin-top: 0; }

.testing3 {
  width: 71.42857%; }
